$cardWidth: 200px;
$cardAmount: 4;
$gapX: 48px;

$color-main: #00acdc;
$tablet-width: 1024px;
$desktop-width: 1200px;

@mixin fonts-medium {
  font-size: 12px;
  line-height: 15px;
  font-weight: 500;
}

@mixin fonts-bold {
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
}

@mixin fonts-big {
  font-size: 16px;
  line-height: 19px;
  font-weight: 700;
}
